html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  color: #34343c;
}

::selection {
  background-color: #9cffa2;
  color: #34343c;
}

p {
  color: #34343c;
  font-size: 1.4rem;
}

.contenedor {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #34343c;
  font-weight: 700;
}

img,
picture {
  width: 100%;
  display: block;
  pointer-events: none;
}
img::selection,
picture::selection {
  background-color: transparent;
}

h1, h2, h3 {
  font-family: "Abril Fatface", cursive;
  margin: 3rem 0;
  font-weight: 800;
  color: #34343c;
  position: relative;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 2.7rem;
}

h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

h4 {
  font-size: 2rem;
}

.seccion {
  margin-top: 12rem !important;
}

.seccion-uno {
  margin-top: 17rem;
}

.margin-top {
  margin-top: 8rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.flex-centrar {
  display: flex;
  justify-content: center;
  align-items: center;
}

.displayNone {
  display: none;
}

.dos-columnas {
  display: grid;
  column-gap: 2rem;
  row-gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

@media (min-width: 768px) {
  .dos-columnas-responsive {
    display: grid;
    column-gap: 2rem;
    row-gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.no-margin {
  margin: 0;
}

.margen-derecha:not(:last-of-type) {
  margin-right: 2rem;
}

.b-r-1 {
  border-radius: 1rem;
}

.no-margin-bottom {
  margin-bottom: 0;
}

@media (min-width: 480px) {
  .grid-tres-columnas-responsive {
    display: grid;
    column-gap: 2rem;
    row-gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .grid-tres-columnas-responsive {
    display: grid;
    column-gap: 2rem;
    row-gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.svg-titulos::before {
  background: url(/build/img/svg-titulo.svg) center/contain no-repeat;
  content: "";
  position: absolute;
  height: 3rem;
  width: 19rem;
  /* left: 0; */
  bottom: -1.5rem;
  z-index: -1;
}

.column {
  flex-direction: column;
}

.boton-pri {
  background-color: #9cffa2;
  color: #34343c;
  font-weight: 800;
  text-decoration: none;
  padding: 2rem 4.5rem;
  text-align: center;
  margin: 2rem 0;
  display: inline-block;
  border: none;
  border-radius: 4rem;
  transition: 0.35s ease-in-out;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.boton-pri:hover {
  background-color: #69ff72;
  cursor: pointer;
}

.boton-transparente {
  background-color: transparent;
  color: #34343c;
  font-weight: 800;
  text-decoration: none;
  padding: 2rem 4.5rem;
  text-align: center;
  margin: 2rem 0;
  display: inline-block;
  border: none;
  border-radius: 4rem;
  transition: 0.35s ease-in-out;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.boton-transparente:hover {
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.boton-negro {
  background-color: #34343c;
  color: #34343c;
  font-weight: 800;
  text-decoration: none;
  padding: 2rem 4.5rem;
  text-align: center;
  margin: 2rem 0;
  display: inline-block;
  border: none;
  border-radius: 4rem;
  transition: 0.35s ease-in-out;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.boton-negro:hover {
  background-color: #1c1c21;
  cursor: pointer;
}

.header .barra-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  height: 8rem;
}
.header .barra-nav-hijo {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
@media (min-width: 768px) {
  .header .barra-nav-hijo {
    justify-content: space-between;
  }
}
.header .logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header .logo img {
  height: 3rem;
  transition: 0.35s ease-in-out;
}
@media (min-width: 768px) {
  .header .logo img {
    display: flex;
    justify-content: left;
  }
}
.header .navegacion-iconos-padre {
  justify-content: right;
}
.header .nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .header .nav {
    display: flex;
    height: calc(100vh - 8rem);
    flex-direction: column;
    align-items: center;
    transition: visibility 0.8s ease-in-out, opacity 0.8s ease-in-out, height 0.8s ease-in-out, background-color 0.8s ease-in-out, padding-top 0.8s ease-in-out, padding-bottom 0.8s ease-in-out, transform 0.8s ease-in-out;
    transform: translateX(-100%);
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 8rem;
    left: 0;
    right: 25%;
    z-index: -1000;
    padding: 2rem;
  }
  .header .nav a {
    color: #34343c;
    display: flex;
    padding: 1.5rem 0 calc(1.5rem - 2px) 0;
    width: 100%;
    justify-content: center;
    position: relative;
  }
  .header .muestra {
    transform: translateX(0);
    background-color: #FFFFFF;
    visibility: visible;
    opacity: 1;
  }
}
.header .enlace-navegacion {
  position: relative;
  width: 50%;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .header .enlace-navegacion {
    width: auto;
    display: block;
    justify-content: end;
  }
}
.header .enlace-navegacion .l {
  width: 0;
  position: absolute;
  bottom: -5px;
  height: 2px;
  background-color: #9cffa2;
  transition: 0.35s ease-in-out;
  border-radius: 3px;
}
.header .enlace-navegacion:hover .l {
  width: calc(100% - 2rem);
}
.header .nav-hijos {
  color: #34343c;
  transition: 0.35s ease-in-out;
  padding: 3rem 0;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
}
@media (min-width: 768px) {
  .header .nav-hijos {
    text-align: right;
    margin-right: 2rem;
  }
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    align-items: center;
    padding: auto;
  }
}

.pantallaCompleta {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(156, 255, 163, 0.5);
  backdrop-filter: blur(1rem);
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  color: transparent;
  transition: 0.8s ease-in-out;
}

.activarPantallaNegra {
  opacity: 1;
  visibility: visible;
}

.fijar-body {
  overflow: hidden;
}

.contenedor-formulario {
  width: 93%;
  max-width: 70rem;
  margin-left: auto;
  margin-right: auto;
}

.formulario fieldset {
  padding: 0;
  border: none;
}
.formulario label {
  font-size: 2rem;
  color: #34343c;
  margin-bottom: 1rem;
}
.formulario input, .formulario textarea {
  border: 1px solid #34343c;
  transition: 0.35s ease-in-out;
  padding: 1rem;
  outline: none;
  width: 100%;
}
.formulario input::placeholder, .formulario textarea::placeholder {
  color: #4e4e4e;
}
.formulario input[type=submit], .formulario textarea[type=submit] {
  border: none;
  padding: 1rem 3rem;
  width: auto;
}
.formulario textarea {
  height: 20rem;
}
.formulario .form-input {
  display: grid;
  margin-bottom: 2rem;
}

.iconos-nav {
  transform: scale(0.6) translateX(4rem);
}
@media (min-width: 768px) {
  .iconos-nav {
    display: none;
  }
}

.span {
  display: block;
  background-color: #34343c;
  border-radius: 2px;
  transition: 0.35s ease-in-out;
}

#menu-toggle {
  width: 10rem;
  height: 10rem;
  position: relative;
  position: relative;
  cursor: pointer;
  border-radius: 5px;
}
#menu-toggle #hamburger {
  position: absolute;
  height: 100%;
  width: 100%;
}
#menu-toggle #hamburger span {
  width: 6rem;
  height: 4px;
  position: relative;
  top: 2.4rem;
  left: 2rem;
  margin: 1rem 0;
}
#menu-toggle #hamburger span:nth-child(1) {
  transition-delay: 0.5s;
}
#menu-toggle #hamburger span:nth-child(2) {
  transition-delay: 0.625s;
}
#menu-toggle #hamburger span:nth-child(3) {
  transition-delay: 0.75s;
}
#menu-toggle #cross {
  position: absolute;
  height: 100%;
  width: 100%;
  transform: rotate(45deg);
}
#menu-toggle #cross span:nth-child(1) {
  height: 0%;
  width: 4px;
  position: absolute;
  top: 10%;
  left: 4.8rem;
  transition-delay: 0s;
}
#menu-toggle #cross span:nth-child(2) {
  width: 0%;
  height: 4px;
  position: absolute;
  left: 10%;
  top: 4.8rem;
  transition-delay: 0.25s;
}

#menu-toggle.open #hamburger span {
  width: 0%;
}
#menu-toggle.open #hamburger span:nth-child(1) {
  transition-delay: 0s;
}
#menu-toggle.open #hamburger span:nth-child(2) {
  transition-delay: 0.125s;
}
#menu-toggle.open #hamburger span:nth-child(3) {
  transition-delay: 0.25s;
}
#menu-toggle.open #cross span:nth-child(1) {
  height: 80%;
  transition-delay: 0.625s;
}
#menu-toggle.open #cross span:nth-child(2) {
  width: 80%;
  transition-delay: 0.375s;
}

.fondo-gradiente {
