@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.fondo-gradiente {
    background: linear-gradient(180deg, v.$blanco 0%, v.$ter 100%);

    
    height: calc(100vh - 8rem);
    
}

.bienvenida {

    .datos-principales {

        
        p {
            font-size: 2.5rem;
            span {
                font-weight: v.$bold;
            }
        }
    }

    
}

.imagen-principal {
    @include m.solo-telefono{
        display: none;
    }
    position: relative;
    // &:hover {

    // }
    img {
        position: absolute;
        width: 30rem;
        bottom: 0;
        right: 0;
        z-index: 1;
        
        object-fit: contain;
    }
    .rectangulo-imagen {
        border-radius: 1rem;
        bottom: 0;
        right: 0;
        height: 16rem;
        position: absolute;
        width: 100%;
        max-width: 35rem;
        background-color: v.$pri;
    }
}